@use '@mview/scss/utilities/functions' as *;

@keyframes waveform-shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.waveform-loader-wrapper {
  display: flex;
  align-items: center;
  gap: 4px;
  position: relative;
  overflow: hidden;
  width: fit-content;
  height: 100px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 100%;
    background: linear-gradient(120deg, transparent 30%, pick-color('secondary', 'steam') 50%, transparent 70%);
    animation: waveform-shimmer 1s infinite linear;
  }

  .wave {
    width: 5px;
    border-radius: 5px;
    background-color: pick-color('primary', 'fog');
    position: relative;
  }
}
